#home{
    border:1px solid transparent;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    align-items: center;
    
    padding: 10% 0% 5% 10%;
}

#resume-button-2{
    color: goldenrod;
}

.detail{
    /* border:1px solid black; */
   margin-left: 8%;
}
span{
    color: gray;
    font-size: 2.6rem;
    margin-left: 5px;
}

.detail p{
    color: var(--color-light);
    line-height: 22px;
    text-align: justify;
}

#resume-button-2{
    margin-top: 50px;
    border-radius: 15px;
}

.detail button>a{  

   /* margin-top: 40px; */
   color: goldenrod;
   background-color: black;
   border-radius: 10px; 
   font-weight: bold;
   font-size: 1.2rem;
   padding: 10px 15px;
   display: flex;
   align-items: center;
   justify-content: center;

    border: 2px solid;
}

.d_icone{
   font-size: 1.2rem;
   margin-left: 4px;
    
  
}

.imgcontainer{
    
    margin: auto;
    border-radius: 250px;
}
