/* media screen */
@media screen  and (max-width:480px){
    #home{
        /* border:1px solid red; */
        display:flex;
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        row-gap: 3rem;
        background-color: var(--color-bg-primary);
        padding: 10% 15% 5% 5%
    }
   
    .detail>h2,span{
        text-align: center;
        font-size: 2rem;
    }
    div>div:nth-child(1)>p{
        text-align:justify;
        margin: 1rem 0;
    }
    
    .detail button{  
        position: relative;
        margin: auto;
        width:90%;
        font-weight: 500;
    }

.imgcontainer{
    position: relative;
    justify-content: center;
  }
    
}

@media screen and (min-width:481px) and (max-width:768px){
     
    #home{
        border:1px solid transparent;
        display:flex;
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        row-gap: 5rem;
        background-color: var(--color-bg-primary);
        padding: 10% 15% 5% 5%
    }
   
    .detail>h2,span{
        text-align: center;
    }
    div>div:nth-child(1)>p{
        text-align:justify;
        margin: 1rem 0;
    }
    
    .detail button{  
        position: relative;
        margin: auto;
        border: 1px solid;
      
    }
  .imgcontainer{
    position: relative;
    margin: auto;
  }
}


@media only screen and (min-width: 769px) and (max-width: 960px) {
    .detail>h2{
        font-size: 1.4rem;
    }
    span{
        font-size: 2.4rem;
    }
    .detail p{
        color: var(--color-light);
        line-height: 20px;
        font-size: 0.9rem;
    }
    
    .detail button{  
        
        padding: 8px 8px;
        text-decoration: none;
        font-weight: bold;
        color:var(--color-white);
        display: inline-block;
        margin: 30px 0;
        border-radius: 5px; 
        display: flex;
        align-items: center;
        justify-content: center;
        gap:1rem;
        width:15rem;
        font-size: 0.8rem; 
    }
    
    .d_icone{
       font-size: 1.8rem;
    }
}

