#projects{
    margin-top: 50px;
    width: 100%;
    margin: auto;
    border: 1px solid transparent;
    background-color: gray;
    color: black;
    /* padding-right: 20px;
    padding-left: 60px; */
    padding-top: 60px;
    
    }
    
    #projects>h1{
        text-align: center;
        font-size: 2.5rem;
        color: orange;
        margin-bottom: 50px;
    }
    
    
    .project-card{
        /* margin-top: 70px; */
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        width: 100%;
        gap: 20px;
        margin-bottom: 90px;
    }
    
    .project-card>div{
        width: 50%;
        border: 1px solid transparent;
        text-align: center;
    }
    

    .project-card>div:nth-child(1){
width: 50%;
    }

    .project-card>div:nth-child(1)>img{
width: 90%;
height: 50%;
    }
    
    
    .project-card>div>a>button{
        /* font-size: 1.2rem; */
        text-align: center;
        padding: 10px 25px;
        /* margin-left: 50px;
        margin-top: -7px; */
        border-radius: 13px;
        background-color: #EBA54C;
        margin-right: 15px;
        margin-top: 50px;
       font-size: 1.2rem;
    }
    
    .project-card>div>p{
        text-align: left;
        padding-left: 30px;
        
    }
    
    .project-card>div>a>button:hover{
        background-color: teal;
        cursor: pointer;
        font-weight: bold;
    }
    